import { useState } from "react";
import CreateBoard from "./Myworkspace/CreateBoard";
import FilterDrop from "./Myworkspace/FilterDrop";
import HideColumn from "./Myworkspace/HideColumn";
import SettingDrop from "./Myworkspace/SettingDrop";
import Sidebar from "./Sidebar";
import { NavLink } from "react-router-dom";

const MyWorkspaces = () => {
  const [group, setgroup] = useState([{}]);
  const [column, setcolumn] = useState([]);
  const [row, setrow] = useState([{}]);
  const [sidebar, setsidebar] = useState(false);

  const handleSidebar = (e) => {
    e.stopPropagation();
    e.preventDefault();
    setsidebar(!sidebar);
  };
  const addNewGroup = () => {
    const item = {
      id: Date.now(),
    };
    setgroup([...group, item]);
  };
  const addNewRow = () => {
    const item = {
      id: Date.now(),
    };
    setrow([...row, item]);
  };
  const addNewColumn = (e) => {
    const item = {
      type: e.target.id,
      id: Date.now(),
    };
    setcolumn([...column, item]);
  };
  return (
    <>
      <div className="padding">
        <Sidebar
          icon={<i className="fa-solid fa-chevron-right"></i>}
          handle={handleSidebar}
        />
        <section className="workspaces">
          <div className="topNav stick">
            <h1>My Workspace</h1>
            <div className="right">
              <div className="input d-fjcac">
                <i className="fa-solid fa-magnifying-glass"></i>
                <input type="text" name="search" placeholder="Search group" />
              </div>

              <div className="dropdown">
                <button
                  className="btn m-0 dropdown-toggle"
                  type="button"
                  id="dropdownMenuButton1"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Add
                  <i className="fa-solid fa-chevron-down ms-3"></i>
                </button>
                <ul
                  className="dropdown-menu"
                  aria-labelledby="dropdownMenuButton1"
                >
                  <li>
                    <a className="dropdown-item" href="#">
                      Action
                    </a>
                  </li>
                  <li>
                    <a className="dropdown-item" href="#">
                      Another action
                    </a>
                  </li>
                  <li>
                    <a className="dropdown-item" href="#">
                      Something else here
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className="line"></div>
          {/* SIDEBAR STARTS */}
          <div className={`sidebarworkspace ${sidebar ? "active" : ""}`}>
            <div>
              <div className="d-flex justify-content-between align-items-center">
                <h2>Worksapce</h2>
                <i
                  className="fa-solid fa-circle-xmark"
                  onClick={handleSidebar}
                ></i>
              </div>
              <div>
                <select name="" id="">
                  <option value="Main workspace"> Main workspace</option>
                </select>
              </div>
              <button
                className="h4"
                data-bs-toggle="modal"
                data-bs-target="#exampleModalWorkspaceAdd"
              >
                <i className="fa-solid fa-plus me-2"></i> Add
              </button>
              <div className="h4">
                <i className="fa-solid fa-magnifying-glass me-2"></i>
                <input type="text" placeholder="Search" />
              </div>
              <div className="line"></div>
              <button className="btn">
                <i className="fa-solid fa-calendar-days me-3"></i>Project 1
                Board
              </button>
            </div>
          </div>
          {/* SIDEBAR ENDS  */}
          <div className={`slideWorkSpace ${sidebar ? "active" : ""}`}>
            <div className="topNav pad">
              <div className="right">
                <div>
                  <h2>
                    Board Name <i className="fa-solid fa-circle-info"></i>
                    <i className="fa-regular fa-star"></i>
                  </h2>
                  <p>Add board description</p>
                </div>
                <div className="bg">
                  <NavLink to="/workspace">
                    <button className={`kanbanBtn active `} id="main">
                      Main Table
                    </button>
                  </NavLink>
                  <NavLink to="/workspace/kanban">
                    <button className={`kanbanBtn `} id="kanban">
                      Kanban
                    </button>
                  </NavLink>
                  <NavLink to="/workspace/calendar">
                    <button className={`kanbanBtn`} id="calendar">
                      Calendar
                    </button>
                  </NavLink>
                </div>
              </div>
              <div className="left">
                <div className="workBtn">
                  <i className="fa-solid fa-eye-slash"></i>
                  <HideColumn />
                </div>
                <div className="workBtn">
                  <i className="fa-solid fa-filter"></i>
                  <FilterDrop />
                </div>
                <div className="workBtn">
                  <i className="fa-solid fa-gear"></i>
                  <SettingDrop />
                </div>
              </div>
            </div>
            <div className="line stickLine m-0"></div>
            <div className={`animate__animated animate__zoomIn`}>
              {group.map((curr, idx) => (
                <div className="mainTableContainer" key={idx}>
                  <div className="customOverflow">
                    <div className="row common  mb-3">
                      <i
                        className=" dropdown-toggle fa-solid fa-ellipsis"
                        type="button"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                      ></i>
                      <ul className="option dropdown-menu ">
                        <div>
                          <button className="btn">Delete</button>
                        </div>
                        <div>
                          <button className="btn">Archive</button>
                        </div>
                        <div>
                          <button className="btn">Copy</button>
                        </div>
                      </ul>
                      <div className="col-md-2 task">
                        <input
                          type="text"
                          placeholder="Group Name"
                          className="border-0"
                        />
                      </div>
                      <div className="col-md-1">Person</div>
                      <div className="col-md-1">Status</div>
                      <div className="col-md-1">Due Date</div>
                      {column.map((curr, idx) => (
                        <div
                          className="col-md-1 position-relative rowElement"
                          key={idx}
                        >
                          <i
                            className=" dropdown-toggle fa-solid fa-ellipsis"
                            type="button"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                          ></i>
                          <ul className="option dropdown-menu ">
                            <div>
                              <button className="btn">Delete</button>
                            </div>
                            <div>
                              <button className="btn">Archive</button>
                            </div>
                            <div>
                              <button className="btn">Copy</button>
                            </div>
                          </ul>
                          {curr.type}
                        </div>
                      ))}
                      <div className="col-md-1">
                        <div className="dropend">
                          <i
                            className="fa-solid fa-circle-plus dropdown-toggle"
                            data-bs-toggle="dropdown"
                            aria-expanded="false"
                          ></i>
                          <ul className=" fixedDrop dropdown-menu">
                            <div className="row rowDropDown">
                              <button id="text" onClick={addNewColumn}>
                                Text
                              </button>
                              <button id="date" onClick={addNewColumn}>
                                Date
                              </button>
                              <button id="number" onClick={addNewColumn}>
                                Numbers
                              </button>
                              <button id="person" onClick={addNewColumn}>
                                People
                              </button>
                              <button id="status" onClick={addNewColumn}>
                                Status
                              </button>
                            </div>
                          </ul>
                        </div>
                      </div>
                    </div>
                    {row.map((curr, idx) => (
                      <div className="row bg hideTrack" key={idx}>
                        <i
                          className=" dropdown-toggle fa-solid fa-ellipsis"
                          type="button"
                          data-bs-toggle="dropdown"
                          aria-expanded="false"
                        ></i>
                        <ul className="option dropdown-menu ">
                          <div>
                            <button className="btn">Delete</button>
                          </div>
                          <div>
                            <button className="btn">Archive</button>
                          </div>
                          <div>
                            <button className="btn">Copy Name</button>
                          </div>
                        </ul>

                        <div className="col-md-2 task">
                          <input type="text" placeholder="Task Name" />
                        </div>
                        <div className="col-md-1">
                          <img
                            src={process.env.PUBLIC_URL + "/Images/human.png"}
                            alt="person"
                          />
                        </div>
                        <div className="col-md-1">
                          <select name="" id="">
                            <option value="Done">Done</option>
                            <option value="Complete" className="bg">
                              Completed
                            </option>
                            <option value="Stuck" className="bg">
                              Stuck
                            </option>
                          </select>
                        </div>
                        <div className="col-md-1 position-relative">
                          <input type="date" />
                        </div>
                        {column.map((curr, idx) => {
                          return curr.type == "status" ? (
                            <div className="col-md-1">
                              <select name="" id="">
                                <option value="Done">Done</option>
                                <option value="Complete" className="bg">
                                  Completed
                                </option>
                                <option value="Stuck" className="bg">
                                  Stuck
                                </option>
                              </select>
                            </div>
                          ) : (
                            <div className="col-md-1 " key={idx}>
                              <input className="border-0" type={curr.type} />
                            </div>
                          );
                        })}
                      </div>
                    ))}
                  </div>
                  <button className="btn " onClick={addNewRow}>
                    Add New Row
                  </button>
                </div>
              ))}
              <button className="btn m-0" onClick={addNewGroup}>
                Add New Group
              </button>
            </div>
          </div>
        </section>

        {/* Modal  */}
        <CreateBoard />
      </div>
    </>
  );
};

export default MyWorkspaces;
