import { useState } from "react";

const NotificationPopup = () => {
  const [notification, setnotification] = useState({
    all: true,
    unRead: false,
    read: false,
  });
  const handleNoti = (e) => {
    if (e.target.id === "allnoti") {
      setnotification({ all: true, unRead: false, read: false });
    }
    if (e.target.id === "unreadnoti") {
      setnotification({ all: false, unRead: true, read: false });
    }
    if (e.target.id === "readnoti") {
      setnotification({ all: false, unRead: false, read: true });
    }
  };
  return (
    <>
      <div
        className="notificationPopup modal fade"
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Notifications
              </h5>
              <button
                type="button"
                className="btnClose d-fjcac"
                data-bs-dismiss="modal"
                aria-label="Close"
              >
                <i className="fa-solid fa-xmark"></i>
              </button>
            </div>
            <div className="modal-body">
              <div className="buttonDiv">
                <button
                  className={`${notification.all ? "active" : ""}`}
                  id="allnoti"
                  onClick={handleNoti}
                >
                  All
                </button>
                <button
                  className={`${notification.unRead ? "active" : ""}`}
                  id="unreadnoti"
                  onClick={handleNoti}
                >
                  Unread
                </button>
                <button
                  className={`${notification.read ? "active" : ""}`}
                  id="readnoti"
                  onClick={handleNoti}
                >
                  Read
                </button>
              </div>
              <div
                className={`allNoti animate__animated  ${
                  notification.all ? "animate__zoomIn" : "d-none"
                }`}
              >
                <div className="notiDiv">
                  <h2>There is 1 board that needs your attention</h2>
                  <div className="notiTab">
                    <button>
                      <i className="fa-solid fa-link"></i>open
                    </button>
                    <div className="dot"></div>
                  </div>
                </div>
                <div className="notiDiv">
                  <h2>There is 1 board that needs your attention</h2>
                  <div className="notiTab">
                    <button>
                      <i className="fa-solid fa-link"></i>open
                    </button>
                    <div className="dot"></div>
                  </div>
                </div>
                <div className="notiDiv">
                  <h2>There is 1 board that needs your attention</h2>
                  <div className="notiTab">
                    <button>
                      <i className="fa-solid fa-link"></i>open
                    </button>
                    <div className="dot"></div>
                  </div>
                </div>
                <div className="notiDiv">
                  <h2>There is 1 board that needs your attention</h2>
                  <div className="notiTab">
                    <button>
                      <i className="fa-solid fa-link"></i>open
                    </button>
                    <div className="dot"></div>
                  </div>
                </div>
                <div className="notiDiv">
                  <h2>There is 1 board that needs your attention</h2>
                  <div className="notiTab">
                    <button>
                      <i className="fa-solid fa-link"></i>open
                    </button>
                    <div className="dot"></div>
                  </div>
                </div>
              </div>
              <div
                className={`unReadNoti animate__animated  ${
                  notification.unRead ? "animate__zoomIn" : "d-none"
                }`}
              >
                <div className="notiDiv">
                  <h2>There is 1 board that needs your attention</h2>
                  <div className="notiTab">
                    <button>
                      <i className="fa-solid fa-link"></i>open
                    </button>
                    <div className="dot"></div>
                  </div>
                </div>
                <div className="notiDiv">
                  <h2>There is 1 board that needs your attention</h2>
                  <div className="notiTab">
                    <button>
                      <i className="fa-solid fa-link"></i>open
                    </button>
                    <div className="dot"></div>
                  </div>
                </div>
              </div>
              <div
                className={`readNoti animate__animated ${
                  notification.read ? "animate__zoomIn" : "d-none"
                }`}
              >
                <div className="notiDiv">
                  <h2>There is 1 board that needs your attention</h2>
                  <div className="notiTab">
                    <button>
                      <i className="fa-solid fa-link"></i>open
                    </button>
                    <div className="dot"></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default NotificationPopup;
