import React, { useState } from "react";
import Settings from "../Settings";
import Sidebar from "../Sidebar";
import { Side } from "./Side";

const User = (props) => {
  const [user, setuser] = useState({
    user: true,
    board: false,
    automations: false,
  });
  const handleUser = (e) => {
    if (e.target.id == "user") {
      setuser({
        user: true,
        board: false,
        automations: false,
      });
    }
    if (e.target.id == "board") {
      setuser({
        user: false,
        board: true,
        automations: false,
      });
    }
    if (e.target.id == "automations") {
      setuser({
        user: false,
        board: false,
        automations: true,
      });
    }
  };
  return (
    <div className="padding">
    <Sidebar/>
    <Settings class="stickSetting"/>
    <Side/>
      <section className={`users customUl animate__animated ${props.user}`}>
        <ul>
          <li
            onClick={handleUser}
            id="user"
            className={`${user.user ? "active" : ""}`}
          >
            User
          </li>
          <li
            onClick={handleUser}
            id="board"
            className={`${user.board ? "active" : ""}`}
          >
            Board Ownership
          </li>
          <li
            onClick={handleUser}
            id="automations"
            className={`${user.automations ? "active" : ""}`}
          >
            Automations Ownership
          </li>
        </ul>
        <div
          className={`board animate__animated animate__zoomIn ${
            user.board ? "" : "d-none"
          }`}
        >
          <h2>Manage Board Owners</h2>
          <p>
            Did someone leave your account? Add a new owner to their boards
            (including shareable and private boards).
          </p>
          <div className="input">
            <label htmlFor="current">Current Owner</label>
            <select name="current" id="current">
              <option value="Search team member">Search team member</option>
            </select>
          </div>
          <div className="input">
            <label htmlFor="new">New Owner</label>
            <select name="new" id="new">
              <option value="Search team member">Search team member</option>
            </select>
          </div>
          <button className="btn">Add New Owner</button>
        </div>
        <div
          className={`user animate__animated animate__zoomIn ${
            user.user ? "" : "d-none"
          }`}
        >
          <div className="top">
            <h3>User Managment</h3>
            <div>
              <a href="#">See billed users</a>
              <a href="#">Manage teams</a>
              <a href="#">Download CSV</a>
            </div>
          </div>
          <label htmlFor="uSearch">User name/email</label>
          <div className="input">
            <div>
              <input type="text" id="uSearch" placeholder="Search" />
              <i className="fa-solid fa-magnifying-glass"></i>
            </div>
            <span>
              <i className="fa-solid fa-filter me-1"></i>Filter
            </span>
          </div>
          <p>Showing: 1 results</p>
          <button className=" d-block btn ms-auto mb-5">Invite</button>
          <div className=" layout">
            <div>
              <input type="checkbox" />
            </div>
            <div>Name</div>
            <div>Email</div>
            <div>User Type</div>
            <div>Products</div>
            <div>Status</div>
            <div>Teams</div>
            <div>Joined</div>
            <div>Invited By</div>
            <div>Last Active</div>
          </div>
          <div className=" layout">
            <div>
              <input type="checkbox" />
            </div>
            <div>David</div>
            <div>david@gmail.com </div>
            <div>Admin</div>
            <div>
              <img src={process.env.PUBLIC_URL + "/Images/logo.svg"} alt="logo" />
            </div>
            <div>Active</div>
            <div>
              <img src={process.env.PUBLIC_URL + "/Images/logo.svg"} alt="logo" />
            </div>
            <div>Jun 22, 2022</div>
            <div>person</div>
            <div>Last Jun 22, 2022</div>
          </div>
        </div>
        <div
          className={`board animate__animated animate__zoomIn ${
            user.automations ? "" : "d-none"
          }`}
        >
          <h2>Manage Automation Ownership</h2>
          <p>
          Transfer automation ownership between users in your account.
          </p>
          <div className="input">
            <label htmlFor="current">Current Owner</label>
            <select name="current" id="current">
              <option value="Search team member">Search team member</option>
            </select>
          </div>
          <div className="input">
            <label htmlFor="new">New Owner</label>
            <select name="new" id="new">
              <option value="Search team member">Search team member</option>
            </select>
          </div>
          <button className="btn">Change Owner</button>
        </div>
      </section>
    </div>
  );
};

export default User;
