import { useState } from "react";
import Settings from '../Settings'
import Sidebar from '../Sidebar'
import {Side}  from './Side'

const Billing = (props) => {
  const [customize, setcustomize] = useState({
    overview: true,
    settings: false,
    invoices: false,
    payment: false,
    billing: false,
  });
  const handleCustom = (e) => {
    if (e.target.id == "overview") {
      setcustomize({
        overview: true,
        settings: false,
        invoices: false,
        payment: false,
        billing: false,
      });
    }
    if (e.target.id == "settings") {
      setcustomize({
        overview: false,
        settings: true,
        invoices: false,
        payment: false,
        billing: false,
      });
    }
    if (e.target.id == "invoices") {
      setcustomize({
        overview: false,
        settings: false,
        invoices: true,
        payment: false,
        billing: false,
      });
    }
    if (e.target.id == "payment") {
      setcustomize({
        overview: false,
        settings: false,
        invoices: false,
        payment: true,
        billing: false,
      });
    }
    if (e.target.id == "billing") {
      setcustomize({
        overview: false,
        settings: false,
        invoices: false,
        payment: false,
        billing: true,
      });
    }
  };
  return (
    <div className="padding">
     <Sidebar/>
        <Settings class="stickSetting"/>
        <Side/>
      <section className={`billing customUl animate__animated ${props.bill}`}>
        <ul>
          <li
            onClick={handleCustom}
            id="overview"
            className={`${customize.overview ? "active" : ""}`}
          >
            Overview
          </li>
          <li
            onClick={handleCustom}
            id="settings"
            className={`${customize.settings ? "active" : ""}`}
          >
            Settings
          </li>
          <li
            onClick={handleCustom}
            id="invoices"
            className={`${customize.invoices ? "active" : ""}`}
          >
            Invoices
          </li>
          <li
            onClick={handleCustom}
            id="payment"
            className={`${customize.payment ? "active" : ""}`}
          >
            Payment Method
          </li>
          <li
            onClick={handleCustom}
            id="billing"
            className={`${customize.billing ? "active" : ""}`}
          >
            Billing Contact
          </li>
        </ul>
        <div
          className={`setting animate__animated animate__zoomIn ${
            customize.settings ? "" : "d-none"
          }`}
        >
          <h3>Settings</h3>
          <p>Billing settings can only be adjusted on a paid plan.</p>
        </div>
        <div
          className={`setting animate__animated animate__zoomIn ${
            customize.invoices ? "" : "d-none"
          }`}
        >
          <h3>Invoices</h3>
          <p>You have no invoices.</p>
        </div>
        <div
          className={`setting animate__animated animate__zoomIn ${
            customize.payment ? "" : "d-none"
          }`}
        >
          <h3>Payment Method</h3>
          <p>You have no payment methods.</p>
        </div>
        <div
          className={`overview animate__animated animate__zoomIn ${
            customize.overview ? "" : "d-none"
          }`}
        >
          <h2>Plan details</h2>
          <div className="border">
            <div className="top row">
              <p className="col-md-3">Current Plane</p>
              <p className="col-md-3">Status</p>
              <p className="col-md-3">Days left in trial</p>
            </div>
            <div className="bottom row">
              <p className="col-md-3">license pro</p>
              <p className="col-md-3">Trial</p>
              <p className="col-md-3">15</p>
              <button className="btn col-md-2">Uprade now</button>
            </div>
          </div>
          <h2>Products</h2>
          <div className="border">
            <div className="top row">
              <p className="col-md-2">Seats</p>
              <p className="col-md-2">Members</p>
              <p className="col-md-2">Guests</p>
              <p className="col-md-2">Viewers</p>
              <p className="col-md-2">On trial</p>
            </div>
            <div className="bottom row">
              <p className="col-md-2">5</p>
              <p className="col-md-2">1</p>
              <p className="col-md-2">15</p>
              <p className="col-md-2">1</p>
              <p className="col-md-2">6 days left</p>
            </div>
          </div>
          <h2>Need Help ?</h2>
          <div className="border">
            <h4>Contact support</h4>
            <p>Our support team is here to help with any questions you may have!  </p>
            <button className="btn">Contact support</button>
          </div>
        </div>

        <div  className={`method animate__animated animate__zoomIn ${customize.billing ? "" : "d-none"}`}>
          <h2>Manage billing contacts</h2>
          <p>Billing contacts will be notified by email regarding billing changes in your account</p>
          <h3>Add billing contact</h3>
          <div className="input">
            <div className="d-flex flex-column me-5">
              <label htmlFor="mem">Choose from your members</label>
              <select  id="mem">
                <option value="Name">Select Name</option>
              </select>
            </div>
            <div className="d-flex flex-column">
              <label htmlFor="add">Or Add a billing contact by email</label>
              <input id="add" type="text" placeholder="Enter Email" />
            </div>
            <button className="btn">Link</button>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Billing;
