import React, { Suspense } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Dashboard from "./Components/Frontend/Dashboard";
import MyWorks from "./Components/Frontend/MyWorks";
import MyWorkspaces from "./Components/Frontend/MyWorkspaces";
import General from "./Components/Frontend/Settings/General";
import Customization from "./Components/Frontend/Settings/Customization";
import Api from "./Components/Frontend/Settings/Api";
import Billing from "./Components/Frontend/Settings/Billing";
import ContentDirectory from "./Components/Frontend/Settings/ContentDirectory";
import CrossAccount from "./Components/Frontend/Settings/CrossAccount";
import Security from "./Components/Frontend/Settings/Security";
import Tidyup from "./Components/Frontend/Settings/Tidyup";
import User from "./Components/Frontend/Settings/Users";
import Personal from "./Components/Frontend/My profile/Personal";
import Status from "./Components/Frontend/My profile/Status";
import Password from "./Components/Frontend/My profile/Password";
import Language from "./Components/Frontend/My profile/Language";
import Session from "./Components/Frontend/My profile/Session";
import Notification from "./Components/Frontend/My profile/Notification";
import Plan from "./Components/Frontend/Pricing/Plan";
import MainBoard from "./Components/Frontend/Myworkspace/MainBoard";
import Calendar from "./Components/Frontend/Myworkspace/Calendar";
import Kanban from "./Components/Frontend/Myworkspace/Kanban";
import Login from "./Components/Frontend/Forms/Login";
import ForgotPassword from "./Components/Frontend/Forms/ForgotPassword";
import Register from "./Components/Frontend/Forms/Register";
import RegisterSecondStep from "./Components/Frontend/Forms/RegisterSecondStep";
import IntakeForms from "./Components/Frontend/Forms/IntakeForms";
import NotFoundPage from "./Components/Frontend/NotFoundPage";
import RegisterThroughInvite from "./Components/Frontend/Forms/RegisterThroughInvite";
import Index from "./Components/LandingPage/Index";

const App = () => {
  return (
    <>
      <BrowserRouter>
        <Suspense>
          <Routes>
            <Route path="/" element={<Dashboard />} />
            <Route path="/myworks" element={<MyWorks />} />

            {/* Nested workspace Routes  */}
            <Route path="/workspace">
              <Route path="/workspace" element={<MyWorkspaces />} />
              <Route path="main-board" element={<MainBoard />} />
              <Route path="calendar" element={<Calendar />} />
              <Route path="kanban" element={<Kanban />} />
            </Route>

            {/* Nested Settings Routes  */}
            <Route path="/settings">
              <Route path="general" element={<General />} />
              <Route path="customization" element={<Customization />} />
              <Route path="users" element={<User />} />
              <Route path="security" element={<Security />} />
              <Route path="api" element={<Api />} />
              <Route path="billing" element={<Billing />} />
              <Route path="tidyup" element={<Tidyup />} />
              <Route path="contentdirectory" element={<ContentDirectory />} />
              <Route path="crossaccount" element={<CrossAccount />} />
            </Route>

            {/* Nested Profile Routes  */}
            <Route path="/myprofile">
              <Route path="personal" element={<Personal />} />
              <Route path="working-status" element={<Status />} />
              <Route path="password" element={<Password />} />
              <Route path="language" element={<Language />} />
              <Route path="notification" element={<Notification />} />
              <Route path="session" element={<Session />} />
            </Route>

            <Route path="/plans" element={<Plan />} />

            {/* Form Routes  */}

            <Route path="/login" element={<Login />} />
            <Route path="/forgotpassword" element={<ForgotPassword />} />
            <Route path="/register" element={<Register />} />
            <Route path="/account" element={<RegisterSecondStep />} />
            <Route path="/user-details" element={<IntakeForms />} />
            <Route
              path="/register-invite"
              element={<RegisterThroughInvite />}
            />

            {/* Landing Page Routes  */}
            {/* <Route path="/" element={<Index/>}/> */}

            <Route path="*" element={<NotFoundPage />} />
          </Routes>
        </Suspense>
      </BrowserRouter>
    </>
  );
};

export default App;
