import { useState } from "react";

const MyWorkFilter = () => {
  const [filter, setfilter] = useState({
    people: false,
    board: false,
    status: false,
    date: false,
  });
  const handleFilter = (e) => {
    if (e.target.id === "people") {
      setfilter({
        people: true,
        board: false,
        status: false,
        date: false,
      });
      if (filter.people == true) {
        setfilter({ people: false });
      }
    }
    if (e.target.id === "board") {
      setfilter({
        people: false,
        board: true,
        status: false,
        date: false,
      });
      if (filter.board == true) {
        setfilter({ board: false });
      }
    }
    if (e.target.id === "status") {
      setfilter({
        people: false,
        board: false,
        status: true,
        date: false,
      });
      if (filter.status == true) {
        setfilter({ status: false });
      }
    }
    if (e.target.id === "date") {
      setfilter({
        people: false,
        board: false,
        status: false,
        date: true,
      });
      if (filter.date == true) {
        setfilter({ date: false });
      }
    }
  };
  return (
    <>
      <div
        className="myworkFilter offcanvas offcanvas-end"
        tabIndex="-1"
        id="offcanvasRightFilter"
        aria-labelledby="offcanvasRightLabel"
      >
        <div className="offcanvas-header">
          <h5 id="offcanvasRightLabel">
            <i className="fa-solid fa-gear"></i>
            Cutomize My Work
          </h5>
          <button
            type="button"
            className="btn-close text-reset"
            data-bs-dismiss="offcanvas"
            aria-label="Close"
          ></button>
        </div>
        <div className="offcanvas-body">
          <p>Choose the boards, columns and people you’d like to see</p>
          <div className={`filters fDrop ${filter.people ? "active" : ""}`}>
            <div
              className="d-flex justify-content-between align-items-center"
              id="people"
              onClick={handleFilter}
            >
              <h2>People</h2>
              <i className="fa-solid fa-chevron-down "></i>
            </div>
            <div className="line"></div>
            <div>
              <h4>Whose items should we show</h4>
              <div>
                <input
                  type="text"
                  placeholder="search name, position, email or team"
                />
              </div>
              <div className=" cardF d-flex justify-content-between align-items-center">
                <div className="d-flex  align-items-center">
                  <i className="fa-solid fa-circle-user"></i>
                  <h6>Shaikh Yamin</h6>
                </div>
                <i className="fa-solid fa-xmark"></i>
              </div>
              <div className=" cardF d-flex justify-content-between align-items-center">
                <div className="d-flex  align-items-center">
                  <i className="fa-solid fa-circle-user"></i>
                  <h6>Shaikh Yamin</h6>
                </div>
                <i className="fa-solid fa-xmark"></i>
              </div>
            </div>
          </div>
          <div className={`board fDrop  ${filter.board ? "active" : ""}`}>
            <div
              className="d-flex justify-content-between align-items-center"
              id="board"
              onClick={handleFilter}
            >
              <h2>Boards</h2>
              <i className="fa-solid fa-chevron-down "></i>
            </div>
            <div className="line"></div>
            <div>
              <h4>Which board should we show?</h4>
              <h6>Hidden Boards</h6>
              <div className=" cardB d-flex justify-content-between align-items-center">
                <label htmlFor="c1">Daivd UI</label>
                <input type="checkbox" name="c1" id="c1" />
              </div>
              <div className=" cardB d-flex justify-content-between align-items-center">
                <label htmlFor="c2">Daivd UI</label>
                <input type="checkbox" name="c2" id="c2" />
              </div>
              <h6>Visible Boards</h6>
              <div className=" cardB d-flex justify-content-between align-items-center">
                <label htmlFor="c3">Daivd UI</label>
                <input type="checkbox" name="c3" id="c3" />
              </div>
              <div className=" cardB d-flex justify-content-between align-items-center">
                <label htmlFor="c4">Daivd UI</label>
                <input type="checkbox" name="c4" id="c4" />
              </div>
            </div>
          </div>
          <div className={`status fDrop ${filter.status ? "active" : ""}`}>
            <div
              className="d-flex justify-content-between align-items-center"
              id="status"
              onClick={handleFilter}
            >
              <h2>Status Column</h2>
              <i className="fa-solid fa-chevron-down "></i>
            </div>
            <div className="line"></div>
            <div>
              <h4>Which status column should we show?</h4>
              <h3>David UI</h3>
              <div className=" cardS d-flex justify-content-between align-items-center">
                <select name="s" id="s">
                  <option value="Status">Status</option>
                </select>
              </div>
              <h3>Sub items of David UI</h3>
              <div className=" cardS d-flex justify-content-between align-items-center">
                <select name="s">
                  <option value="Priority">Priority</option>
                </select>
              </div>
            </div>
          </div>
          <div className={`status fDrop ${filter.date ? "active" : ""}`}>
            <div
              className="d-flex justify-content-between align-items-center"
              id="date"
              onClick={handleFilter}
            >
              <h2>Date Column</h2>
              <i className="fa-solid fa-chevron-down "></i>
            </div>
            <div className="line"></div>
            <div>
              <h4>Which status column should we show?</h4>
              <h3>David UI</h3>
              <div className=" cardS d-flex justify-content-between align-items-center">
                <select name="s">
                  <option value="Date">Date</option>
                </select>
              </div>
              <h3>Sub items of David UI</h3>
              <div className=" cardS d-flex justify-content-between align-items-center">
                <select name="s">
                  <option value="Date">Date</option>
                </select>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default MyWorkFilter;
