import { useState } from 'react'
import Settings from '../Settings'
import Sidebar from '../Sidebar'
import {Side}  from './Side'

const Tidyup = (props) => {
    const [customize, setcustomize] = useState({
        boards: true,
        archived: false,
        items: false,
    });
    const handleCustom = (e) => {
        if (e.target.id == "boards" || e.target.id == "back") {
            setcustomize({
                items: false,
                boards: true,
                archived: false,
            });
        }
        if (e.target.id == "archived") {
            setcustomize({
                items: false,
                boards: false,
                archived: true,
            });
        }
        if (e.target.id == "items") {
            setcustomize({
                items: true,
                boards: false,
                archived: false,
            });
        }
    }
    return (
        <div className='padding'>
         <Sidebar/>
        <Settings class="stickSetting"/>
        <Side/>
            <section className={`tidyup customUl animate__animated ${props.tidy}`}>
                <h2>Organize your Data</h2>
                <p>Use this tool to tidy up all the Boards and Items in your account (except Private and Shareable Boards).</p>
                <ul>
                    <li
                        onClick={handleCustom}
                        id="boards"
                        className={`${customize.boards ? "active" : ""}`}
                    >
                        Boards
                    </li>
                    <li
                        onClick={handleCustom}
                        id="archived"
                        className={`${customize.archived ? "active" : ""}`}
                    >
                        Archived Boards
                    </li>

                </ul>
                <div className={`boards animate__animated animate__zoomIn ${customize.boards ? "" : "d-none"}`}>
                    <div className="top row">
                        <p className='col-md-3'>BOARDS</p>
                        <p className='col-md-3'>LAST UPDATED</p>
                        <p className='col-md-3'>CREATED</p>
                    </div>
                    <div className="bottom row">
                        <div className='col-md-3 custom'>
                            <h3>Warner UXUI</h3>
                            <h4>Created by: <span>David Warner</span></h4>
                        </div>
                        <p className='col-md-3'>5 minutes ago</p>
                        <p className='col-md-3'>5 days ago</p>
                        <div className='col-md-3'>
                            <button className='me-4' id='items' onClick={handleCustom}><i className="fa-solid fa-cloud-arrow-up me-2"></i>SEE ITEMS</button>
                            <button className='ms-3'><i className="fa-solid fa-box-archive me-2"></i>ARCHIVE</button>
                        </div>
                    </div>
                </div>
                <div className={`boards animate__animated animate__zoomIn ${customize.archived ? "" : "d-none"}`}>
                    <div className="top row">
                        <p className='col-md-3'>BOARDS</p>
                        <p className='col-md-3'>LAST UPDATED</p>
                        <p className='col-md-3'>CREATED</p>
                    </div>
                    <div className="bottom row">
                        <div className='col-md-3 custom'>
                            <h3>Yamin UXUI</h3>
                            <h4>Created by: <span>Yamin shaikh</span></h4>
                        </div>
                        <p className='col-md-3'>60 minutes ago</p>
                        <p className='col-md-3'>50 days ago</p>
                        <div className='col-md-3'>
                            <button className='me-4'><i className="fa-solid fa-cloud-arrow-up me-2"></i>SEE ITEMS</button>
                            <button className='ms-3'><i className="fa-solid fa-box-archive me-2"></i>ARCHIVE</button>
                        </div>
                    </div>
                </div>
                <div className={`boards animate__animated animate__zoomIn ${customize.items ? "" : "d-none"}`}>
                    <button className='arrow' onClick={handleCustom} id='back'><i ClassName="fa-solid fa-arrow-left-long"></i>Items for Test UXUI</button>
                    <div className="top row">
                        <p className='col-md-3'>BOARDS</p>
                        <p className='col-md-3'>LAST UPDATED</p>
                        <p className='col-md-3'>CREATED</p>
                    </div>
                    <div className="bottom row">
                        <div className='col-md-3 custom'>
                            <h3>Test UXUI</h3>
                            <h4>Created by: <span>Chrish Johns</span></h4>
                        </div>
                        <p className='col-md-3'>6 minutes ago</p>
                        <p className='col-md-3'>6 days ago</p>
                        <div className='col-md-3'>
                            <button className='me-4'><i className="fa-solid fa-cloud-arrow-up me-2"></i>ARCHIVE</button>
                            <button className='ms-3'><i ClassName="fa-solid fa-trash-can me-2"></i>DELETE</button>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Tidyup