import { useState } from "react";
import FilterDrop from "./FilterDrop";
import HideColumn from "./HideColumn";
import SettingDrop from "./SettingDrop";
import Sidebar from "../Sidebar";
import { NavLink } from "react-router-dom";

const Kanban = () => {
  const [sidebar, setsidebar] = useState(false);
  const [empty, setempty] = useState([{}]);
  const [progress, setprogress] = useState([{}]);
  const [stuck, setstuck] = useState([{}]);
  const [completed, setcompleted] = useState([{}]);

  const handleSidebar = (e) => {
    e.stopPropagation();
    e.preventDefault();
    setsidebar(!sidebar);
  };

  const handleEmpty = () => {
    const item = {
      id: Date.now(),
    };
    setempty([...empty, item]);
  };
  const handleProgress = () => {
    const item = {
      id: Date.now(),
    };
    setprogress([...progress, item]);
  };
  const handleStuck = () => {
    const item = {
      id: Date.now(),
    };
    setstuck([...stuck, item]);
  };
  const handleCompleted = () => {
    const item = {
      id: Date.now(),
    };
    setcompleted([...completed, item]);
  };
  return (
    <>
      <div className="padding">
        <Sidebar
          icon={<i className="fa-solid fa-chevron-right"></i>}
          handle={handleSidebar}
        />

        <div className="workspaces">
          <div className="topNav stick">
            <h1>My Workspace</h1>
            <div className="right">
              <div className="input d-fjcac">
                <i className="fa-solid fa-magnifying-glass"></i>
                <input type="text" name="search" placeholder="Search group" />
              </div>

              <div className="dropdown">
                <button
                  className="btn m-0 dropdown-toggle"
                  type="button"
                  id="dropdownMenuButton1"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Add
                  <i className="fa-solid fa-chevron-down ms-3"></i>
                </button>
                <ul
                  className="dropdown-menu"
                  aria-labelledby="dropdownMenuButton1"
                >
                  <li>
                    <a className="dropdown-item" href="#">
                      Action
                    </a>
                  </li>
                  <li>
                    <a className="dropdown-item" href="#">
                      Another action
                    </a>
                  </li>
                  <li>
                    <a className="dropdown-item" href="#">
                      Something else here
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className="line"></div>
          {/* SIDEBAR STARTS */}
          <div className={`sidebarworkspace ${sidebar ? "active" : ""}`}>
            <div className="d-flex justify-content-between align-items-center">
              <h2>Worksapce</h2>
              <i
                className="fa-solid fa-circle-xmark"
                onClick={handleSidebar}
              ></i>
            </div>
            <div>
              <select name="" id="">
                <option value="Main workspace"> Main workspace</option>
              </select>
            </div>
            <button
              className="h4"
              data-bs-toggle="modal"
              data-bs-target="#exampleModalWorkspaceAdd"
            >
              <i className="fa-solid fa-plus me-2"></i> Add
            </button>
            <div className="h4">
              <i className="fa-solid fa-magnifying-glass me-2"></i>
              <input type="text" placeholder="Search" />
            </div>
            <div className="line"></div>
            <button className="btn">
              <i className="fa-solid fa-calendar-days me-3"></i>Project 1 Board
            </button>
          </div>
          {/* SIDEBAR ENDS  */}
          <div className={`slideWorkSpace ${sidebar ? "active" : ""}`}>
            <div className="topNav pad">
              <div className="right">
                <div>
                  <h2>
                    Board Name <i className="fa-solid fa-circle-info"></i>
                    <i className="fa-regular fa-star"></i>
                  </h2>
                  <p>Add board description</p>
                </div>
                <div className="bg">
                  <NavLink to="/workspace">
                    <button className={`kanbanBtn`} id="main">
                      Main Table
                    </button>
                  </NavLink>
                  <NavLink to="/workspace/kanban">
                    <button className={`kanbanBtn active `} id="kanban">
                      Kanban
                    </button>
                  </NavLink>
                  <NavLink to="/workspace/calendar">
                    <button className={`kanbanBtn`} id="calendar">
                      Calendar
                    </button>
                  </NavLink>
                </div>
              </div>
              <div className="left">
                <div className="workBtn">
                  <i className="fa-solid fa-eye-slash"></i>
                  <HideColumn />
                </div>
                <div className="workBtn">
                  <i className="fa-solid fa-filter"></i>
                  <FilterDrop />
                </div>
                <div className="workBtn">
                  <i className="fa-solid fa-gear"></i>
                  <SettingDrop />
                </div>
              </div>
            </div>
            <div className="line stickLine m-0"></div>
            <div className="kanban">
              <h2>Group Name</h2>
              <div className="row">
                <div className="col-md-3">
                  <div className="empty commonDiv">
                    <div>
                      <h4>
                        Empty
                        <i
                          className="fa-solid fa-circle-plus "
                          onClick={handleEmpty}
                        ></i>
                      </h4>
                    </div>
                    <div className="input">
                      {empty.map((curr, idx) => (
                        <input type="text" placeholder="Task Name" key={idx} />
                      ))}
                    </div>
                  </div>
                </div>
                <div className="col-md-3">
                  <div className="Inprogress commonDiv">
                    <div>
                      <h4>
                        In Progress
                        <i
                          className="fa-solid fa-circle-plus"
                          onClick={handleProgress}
                        ></i>
                      </h4>
                    </div>
                    <div className="input">
                      {progress.map((curr, idx) => (
                        <input type="text" placeholder="Task Name" key={idx} />
                      ))}
                    </div>
                  </div>
                </div>
                <div className="col-md-3">
                  <div className="stuck commonDiv">
                    <div>
                      <h4>
                        Stuck
                        <i
                          className="fa-solid fa-circle-plus"
                          onClick={handleStuck}
                        ></i>
                      </h4>
                    </div>
                    <div className="input">
                      {stuck.map((curr, idx) => (
                        <input type="text" placeholder="Task Name" key={idx} />
                      ))}
                    </div>
                  </div>
                </div>
                <div className="col-md-3">
                  <div className="completed commonDiv">
                    <div>
                      <h4>
                        Completed
                        <i
                          className="fa-solid fa-circle-plus"
                          onClick={handleCompleted}
                        ></i>
                      </h4>
                    </div>
                    <div className="input">
                      {completed.map((curr, idx) => (
                        <input type="text" placeholder="Task Name" key={idx} />
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Kanban;
