import { useState } from "react";
import IntakeFormPurpose from "./IntakeFormPurpose";
import IntakeFormsMembers from "./IntakeFormsMembers";
import IntakeFormsPlace from "./IntakeFormsPlace";

const IntakeForms = () => {
  const [form, setform] = useState({
    place: true,
    purpose: false,
    members: false,
  });
  const [handleForm, sethandleForm] = useState({
    work: false,
    personal: false,
    nonprofit: false,
    school: false,
  });
  const handleSteps = (e) => {
    if (e.target.id == "place") {
      setform({
        place: true,
        purpose: false,
        members: false,
      });
    }
    if (e.target.id == "purpose") {
      setform({
        place: false,
        purpose: true,
        members: false,
      });
    }
    if (e.target.id == "members") {
      setform({
        place: false,
        purpose: false,
        members: true,
      });
    }
  };
  const handleFormPlace = (e) => {
    if (e.target.id == "work") {
      sethandleForm({
        work: true,
        personal: false,
        nonprofit: false,
        school: false,
      });
    }
    if (e.target.id == "school") {
      sethandleForm({
        work: false,
        personal: false,
        nonprofit: false,
        school: true,
      });
    }
    if (e.target.id == "personal") {
      sethandleForm({
        work: false,
        personal: false,
        nonprofit: false,
        school: false,
      });
    }
    if (e.target.id == "nonprofits") {
      sethandleForm({
        work: false,
        personal: false,
        nonprofit: true,
        school: false,
      });
    }
  };
  return (
    <>
      <header className="loginHeader">
        <div>
          <img src={process.env.PUBLIC_URL + "/Images/logo.svg"} alt="icon" />
        </div>
        <h1>Fireboard</h1>
      </header>
      <div className="limiter">
        <div className="container-login100">
          <div
            className={`wrapper animate__animated animate__fadeInLeft ${
              form.place ? "" : "d-none"
            }`}
          >
            <div className="content">
              <h2>Hey, What brings you here today?</h2>
              <div className="d-flex">
                <div className="input">
                  <label htmlFor="work" onClick={handleFormPlace}>
                    <input type="radio" name="place" id="work" />
                    Work
                  </label>
                </div>
                <div className="input">
                  <label htmlFor="personal" onClick={handleFormPlace}>
                    <input type="radio" name="place" id="personal" />
                    Personal
                  </label>
                </div>
                <div className="input">
                  <label htmlFor="school" onClick={handleFormPlace}>
                    <input type="radio" name="place" id="school" />
                    School
                  </label>
                </div>
                <div className="input">
                  <label htmlFor="nonprofits" onClick={handleFormPlace}>
                    <input type="radio" name="place" id="nonprofits" />
                    Nonprofits
                  </label>
                </div>
              </div>
              <IntakeFormsPlace
                work={handleForm.work}
                nonprofit={handleForm.nonprofit}
                school={handleForm.school}
              />
              <div className="forward">
                <button className="btn" onClick={handleSteps} id="purpose">
                  Contine
                </button>
              </div>
            </div>
          </div>
          {/* Second Wrapper  */}
          <IntakeFormPurpose
            customClass={`${form.purpose ? "" : "d-none"}`}
            handle={handleSteps}
          />

          {/* Third wrapper  */}
          <IntakeFormsMembers
            customClass={`${form.members ? "" : "d-none"}`}
            handle={handleSteps}
          />
        </div>
      </div>
    </>
  );
};

export default IntakeForms;
